<template>
  <navigation-bar>
    <div slot="middle_content">
      店铺列表
    </div>
  </navigation-bar>
  <group>
    <cell v-for="shop in shops" :title=shop.name :link=buildLink(shop.id) value="Protected" inline-desc="sss">
      <div class="avatar" slot="icon">
        <img v-bind:src="shop.avatar" alt="" />
      </div>
    </cell>
  </group>
  <Tabbar></Tabbar>
</template>
<script>
  import { Cell, Group } from 'vux'

  export default {
    data () {
      return {
        shops: []
      }
    },
    route: {
      data (transition) {
        this.$http.get('http://jiancan.me/api/u1/shops.json', { }).then(function (response) {
          transition.next({
            shops: response.data
          })
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    methods: {
      buildLink (shopId) {
        return '/shops/' + shopId
      }
    },
    components: {
      'Tabbar': require('../components/tabbar.vue'),
      'NavigationBar': require('../components/navigation-bar.vue'),
      Group,
      Cell
    }
  }
</script>
<style lang="scss">
  .avatar {
    width: 80px;
    height: 80px;
    margin-right: 15px;

    img {
      width: 80px;
      height: 80px;
    }
  }
</style>



